.Details {
  display: flex;
  flex-direction: column;
  border: 2px solid var(--main-blue);
  border-radius: 10px;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: var(--light);
  top: 0;
  color: var(--main-blue);
  box-shadow: 0 0 0.5rem var(--main-blue);
  font-size: 0.8rem;
  font-family: var(--font);
  animation: fade-in 2s;

  &-headline {
    text-align: center;
    font-weight: 500;
  }

  &-tables {
    display: flex;
    justify-content: space-evenly;
  }
}
